@import url(https://fonts.googleapis.com/css?family=Indie+Flower);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.game-grid {
    display: grid;
    grid-template-columns: auto auto auto;
    position: absolute;
    top: 12rem;
}
.strikethrough{
    display: none;
    background-color: rgb(208 59 59 / 82%);
    position: absolute;
    z-index: 100;
}

.row-general{
    display: block;
    width: 20rem;
    left: -1rem;
    height: 8px;
}

.row-1-strike {
    top: 2.8rem;
}
.row-2-strike {
    top: 8.9rem;
}
.row-3-strike {
    top: 15rem;
}

.col-general{
    display: block;
    height: 20rem;
    top: -1rem;
    width: 8px;
}

.col-1-strike {
    left: 2.6rem; 
}
.col-2-strike {
    left: 8.7rem;
}
.col-3-strike {
    left: 14.8rem;
}

.diag-left-4-strike {
    display: block;
    transform: rotate(45deg);
    width: 26rem;
    height: 8px;
    top: 8.7rem;
    left: -4rem;
}
.diag-right-4-strike {
    display: block;
    transform: rotate(-45deg);
    width: 24rem;
    height: 8px;
    top: 8.5rem;
    left: -2.8rem;
}
.tic-panel{
    width: 6rem;
    height: 6rem;
    border: 3px solid black;
    font-size: 6rem;
    background: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
}
.panel_1{
    border-top: none;
    border-left: none;
}
.panel_2{
    border-top: none;
}
.panel_3 {
    border-top: none;
    border-right: none;
}
.panel_4{
    border-left: none;
}
.panel_6{
    border-right: none;
}
.panel_7{
    border-left: none;
    border-bottom: none;
}
.panel_8{
    border-bottom: none;
}
.panel_9{
    border-right: none;
    border-bottom: none;
}
.stats{
    position: absolute;
    top: 52rem;
    padding-bottom: 4rem;
  }
.heading{
    font-size: 3rem;
  }
  .turn-indicator {
    font-size: 2rem;
  }
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body {
  margin: 0;
  padding: 0;
  background: lightgoldenrodyellow;
}
.paper {
  position: absolute;
  height: 50rem;
  width: 100%;
  background: rgba(255,255,255,0.9);
  margin: -275px -225px;
  left: 14rem;
  top: 17rem;
  box-shadow: 0px 0px 5px 0px #888;
}
.paper::before {
  content: '';
  position: absolute;
  left: 45px;
  height: 100%;
  width: 2px;
  background: rgba(255,0,0,0.4);
}
.lines {
  margin-top: 40px;
  height: calc(100% - 40px);
  width: 100%;
  background-image: repeating-linear-gradient(white 0px, white 24px, steelblue 25px);
}
.text {
  position: absolute;
  top: 65px;
  left: 55px;
  bottom: 10px;
  right: 10px;
  line-height: 25px;
  font-family: 'Indie Flower';
  overflow: hidden;
  outline: none;
}
.content{
  display: flex;
  justify-content: center;
}
.holes {
  position: absolute;
  left: 10px;
  height: 25px;
  width: 25px;
  background: lightgoldenrodyellow;
  border-radius: 50%;
  box-shadow: inset 0px 0px 2px 0px #888;
}
.hole-top {
  top: 10%;
}
.hole-middle {
  top: 50%;
}
.hole-bottom {
  bottom: 10%;
}

.btn-clearField{
  width: 12rem;
  height: 4rem;
  font-size: 2rem;
  top: 28rem;
  position: relative;
}
.footer{
  position: absolute;
  top: 46.8rem;
  right: 2rem;
}
